export default {
  data() {
    return {
      // 传给子组件的
      scrollTop: 0
    };
  },

  created() {
    // 页面滚动监听器
    this.listenerFunction();
  },

  beforeDestroy() {
    // 去除监听器(第一个参数是要移除监听的事件类型，第二个参数是要移除的事件处理函数（监听器）)
    document.removeEventListener("scroll", this.listenerFunction);
  },

  methods: {
    listenerFunction(e) {
      // 添加监听器，为监听器绑定 handleScroll() 方法，因为属于在主页面使用滚动事件，所以可以对 document 添加监听器
      document.addEventListener('scroll', this.handleScroll, true);
    },

    handleScroll() {
      // bug：从其他组件回来后会报错(Uncaught TypeError: Cannot read properties of null (reading 'scrollTop') at Proxy.handleScroll)
      if (this.$route.path.indexOf('DiscoverLatestMusic') !== -1) {
        // console.log(this.$refs.big_container.scrollTop);
        // 每滚动一段距离输出一次，DiscoverNewSong.vue 大约为 10 首的距离，DiscoverNewDisc.vue 大约为 3 个专辑多一点的距离
        if (this.$refs.big_container.scrollTop % 800 == 0) {
          this.scrollTop = this.$refs.big_container.scrollTop; // console.log('父：' + this.scrollTop);
        }
      }
    }

  }
};